"use strict";
var router_1 = require("@angular/router");
var loggedin_guard_1 = require("./guards/loggedin.guard");
var login_component_1 = require("./pages/login/login.component");
var courses_component_1 = require("./pages/courses/courses.component");
var edit_course_component_1 = require("./pages/courses/edit-page/edit-course.component");
var appRoutes = [
    { path: '', redirectTo: 'courses', pathMatch: 'full' },
    { path: 'courses', component: courses_component_1.CoursesComponent, canActivate: [loggedin_guard_1.LoggedInGuard] },
    { path: 'courses/:id', component: edit_course_component_1.EditCourseComponent, canActivate: [loggedin_guard_1.LoggedInGuard] },
    { path: 'login', component: login_component_1.LoginComponent }
];
exports.routing = router_1.RouterModule.forRoot(appRoutes, { useHash: true });



